import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { HeaderButton, List, Cell, Link, platform, CellButton, FormStatus, Header, SimpleCell, InfoRow, View, FixedLayout, Search, Separator, Tabs, TabsItem, RichCell, Title, Subhead, Caption, Text, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28GhostSimleOutline from '@vkontakte/icons/dist/28/ghost_simle_outline';
import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';


import minecraft from '../img/minecraft.png';
import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import './Persik.css';
import './Tochka.css';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={false}>
				<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="task">
				{Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Minecraft
		</PanelHeader>
                <Div style={{left:100, marginTop:10}}>

<Group header={<Header mode="secondary">АКТИВНО ДО 18.09.2020</Header>}></Group>
 <Div><Cell before={<Avatar mode="app" src={minecraft} />} description="Песочница">Minecraft</Cell></Div>
<Div style={{ marginTop:-35}}>
                        <Cell
          description="Minecraft Pocket Edition"
        >
          Название
        </Cell>
        </Div>
        <Div style={{marginTop:-40}}>

                                <Cell
          description="Не указана"
        >
          Версия
        </Cell>
        </Div>
 <Separator wide />

<Header mode="secondary">Задачи: <Div style={{ paddingTop: -35, paddingBottom: 10 }}>
           <h5>1. Узнать оптимальный настройки вашего устройства для комфортной игры. <br/> 2. Проверить предельную высоту до начала зависания (если зависает).</h5>
          </Div></Header>

        <Cell>
        
<Subhead weight="semibold" style={{ marginBottom: 16 }}>ВЫПЛАТА:<div className="price_task_1" >425 РУБ</div></Subhead>
	
        </Cell>
  
	<center>

                     <Div>
       <Button before={<Icon28DocumentOutline/>} size="l">Перейти к документации</Button>
     </Div>
      </center>
</Div>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
